export default function calendar(data: Record<string, any>) {
  const { time = '', place = '', comment = '', subject = '', topReceivers = [] } = data.metadata || {};
  const texts = [subject];
  if (time) texts.push(time);
  if (place) texts.push(place);
  texts.push(topReceivers.map(rec => rec.name || '').join(' '));
  if (comment) texts.push(comment);
  return texts.join('\n');
}
